import React, { Component } from 'react'
import Faceithavewemet from '../assets/img/faceithavewemet.PNG'
import AnimalDetective from '../assets/img/animaldetective.PNG'
import LownoWebsite from '../assets/img/LOWNO.PNG'
import eBuildbe from '../assets/img/ebuild.png'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHtml5 } from '@fortawesome/free-brands-svg-icons';
import { faJsSquare } from '@fortawesome/free-brands-svg-icons';
import { faCss3Alt } from '@fortawesome/free-brands-svg-icons';
import { faWordpress } from '@fortawesome/free-brands-svg-icons';
import { faPhp } from '@fortawesome/free-brands-svg-icons';
import { faGithubSquare } from '@fortawesome/free-brands-svg-icons';
import { faBootstrap } from '@fortawesome/free-brands-svg-icons';

export class InnerGallery extends Component {



    
    render() {

        let Work =(props) => {
            return (
            <div className="project-container border-bottom">
                <div>
                    <img className="rounded border" alt={props.imageAlt} src={props.image}></img>
                </div>

                <div>
                    <h5 className="font-weight-bolder my-2">{props.title}</h5>
                    <span>
                        <FontAwesomeIcon icon={props.tech1}/>   {" "}
                        <FontAwesomeIcon icon={props.tech2}/>   {" "}
                        <FontAwesomeIcon icon={props.tech3}/>   {" "}
                        <FontAwesomeIcon icon={props.tech4}/>   {" "}
                        <FontAwesomeIcon icon={props.tech5}/>   {" "}
                        <FontAwesomeIcon icon={props.tech6}/>   {" "}
                        </span>
                    <p className="my-2">{props.info}</p>
                    <div className={props.collabClass}>
                    This project was created in co-operation with {props.collab}
                    </div>
                    <a className={props.viewCodeClass} href={props.viewCode}  target="_blank" rel="noopener noreferrer">View Code</a>
                    <a className={props.viewProjectClass} href={props.viewProject} target="_blank" rel="noopener noreferrer">View Project</a>
                </div>
                            
            </div>
              
          )
        }
        


        return (
            <div className="container w-100">
                    <div className="w-100 h-auto pt-5 my-5">
                        <h2 className="text-center">I'm currently looking for work!</h2>
                        <p className="text-center mt-0">Feel free to contact me at alfanoangeloc@gmail.com.</p>
                        <Work 

                        title="Ebuild.be" 
                        image={eBuildbe} 
                        tech1={faJsSquare}
                        tech2={faCss3Alt}
                        tech3={faWordpress}
                        tech4={null}
                        tech5={null}
                        collabClass="w-100 m-0 pl-0"
                        collab="appart.agency"
                        imageAlt="Ebuild Example image"
                        info="A website that has houses for sale. I did frontend adjustments and tweaks." 
                        viewCodeClass="hidden"
                        viewProject="https://www.e-build.be/"
                        />


                        <Work 

                        title="Lowno - Non alcoholic drinks" 
                        image={LownoWebsite} 
                        tech1={faHtml5}
                        tech2={faJsSquare}
                        tech3={faCss3Alt}
                        tech4={faWordpress}
                        tech5={faPhp}
                        collabClass="w-100 m-0 pl-0"
                        collab="appart.agency"
                        imageAlt="Lowno example "
                        info="A website with recipes of non alcoholic coctails! There is also a webshop included. While the UX came from Appart.agency i did all the frontend work." 
                        viewCodeClass="hidden"
                        viewProject="http://www.low-no.com.transurl.nl/"
                        />
                        
                        <Work 

                        title="Animal Detective" 
                        image={AnimalDetective} 
                        tech1={faHtml5}
                        tech2={faJsSquare}
                        tech3={faCss3Alt }
                        tech4={faGithubSquare }
                        tech5={faBootstrap}
                        tech6={null}
                        collabClass="w-100 m-0 pl-0"
                        collab="Jiří Hausner and Patrik Žižka"
                        imageAlt="Animal detective example image"
                        info="This website helps you find lost pets, it can also be used to post pets and will also offer a marketplace. The launch date is yet to be announced. I created the Design and 
                        i did all the frontend for this website." 
                        viewCodeClass="hidden"
                        viewCode="https://github.com/AngeloAlfanoc/detective"
                        viewProjectClass="hidden" 
                        viewProject="https://angeloalfanoc.github.io/detective/"
                        />


                        <Work 
                        title="Faceit have we met?" 
                        image={Faceithavewemet} 
                        tech1={faHtml5}
                        tech2={faJsSquare}
                        tech3={faCss3Alt }
                        tech4={ faGithubSquare }
                        tech5={ null }
                        tech6={ null }
                        collabClass="hidden"
                        imageAlt="Faceit have we met example image"
                        info="The app allows users to find matches played with each other. Since there wasn't a tool like this yet i decided to develop it myself. This uses the faceit API and is completely generated on the client side." 
                        viewCode="https://github.com/AngeloAlfanoc/FaceitHaveWeMet"
                        viewProject="https://angeloalfanoc.github.io/FaceitHaveWeMet/"
                        />

                    </div>
            </div>

            
        )
    }
    
}

export default InnerGallery



