import React, { Component } from 'react'


import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faHtml5 } from '@fortawesome/free-brands-svg-icons';
import { faJsSquare } from '@fortawesome/free-brands-svg-icons';
import { faCss3Alt } from '@fortawesome/free-brands-svg-icons';
import { faWordpress } from '@fortawesome/free-brands-svg-icons';
import { faPhp } from '@fortawesome/free-brands-svg-icons';
import { faGithubSquare } from '@fortawesome/free-brands-svg-icons';
import { faAws } from '@fortawesome/free-brands-svg-icons';
import { faBootstrap } from '@fortawesome/free-brands-svg-icons';
import { faAdobe} from '@fortawesome/free-brands-svg-icons';
import { faJoomla} from '@fortawesome/free-brands-svg-icons';
import { faNpm} from '@fortawesome/free-brands-svg-icons';
import { faLess } from '@fortawesome/free-brands-svg-icons';
import { faNode } from '@fortawesome/free-brands-svg-icons';
import { faReact } from '@fortawesome/free-brands-svg-icons';
import { faSass } from '@fortawesome/free-brands-svg-icons';




import { faCircle } from '@fortawesome/free-solid-svg-icons';

export class Skills extends Component {



    
    render() {

        let Skill =(props) => {
         

            return (
     
                    <div className="outer-skills">
                        <div className="logo">
                            <FontAwesomeIcon icon={ props.tech} size="4x"/> 
                        </div>
                        <div className="stars">
                            <FontAwesomeIcon icon={ props.star1 } /> 
                            <FontAwesomeIcon icon={ props.star2 }/> 
                            <FontAwesomeIcon icon={ props.star3 } /> 
                            <FontAwesomeIcon icon={ props.star4 } /> 
                            <FontAwesomeIcon icon={ props.star5 }  /> 
                        </div>
                    </div>
             
              
          )
        }

        return (
            <div className="w-75 m-auto bg-light ">

                <div className="d-flex flex-column justify-content-center align-content-center h-100">
                    <h2 className="text-left pl-5 ml-2 mt-5 ">Knowledge</h2>
                    <p  className="text-left pl-5 ml-2 mb-5  ">As a junior developer i'm trying to learn as much as possible. Currently working hard to learn Reactjs! This portfolio has been created in Reactjs!</p>
                    <div className="fullskillset">
                    <Skill tech={ faAdobe}          star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }   star5= { faCircle } />
                    <Skill tech={ faHtml5 }         star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }   star5= { faCircle } />
                    <Skill tech={ faJsSquare }      star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }   star5= { faCircle } />
                    <Skill tech={ faCss3Alt }       star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }   star5= { faCircle } />
                    <Skill tech={ faWordpress }     star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }  />
                    <Skill tech={ faGithubSquare }  star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }  />
                    <Skill tech={ faBootstrap }     star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }  />
                    <Skill tech={ faNpm }           star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }  />
                    <Skill tech={ faSass }          star1= { faCircle }  star2= { faCircle }    star3= { faCircle }   star4= { faCircle }  />
                    <Skill tech={ faPhp }           star1= { faCircle }  star2= { faCircle }    star3= { faCircle } />
                    <Skill tech={ faAws }           star1= { faCircle }  star2= { faCircle }    star3= { faCircle } />
                    <Skill tech={ faJoomla}         star1= { faCircle }  star2= { faCircle }    star3= { faCircle } />
                    <Skill tech={ faLess }          star1= { faCircle }  star2= { faCircle }    star3= { faCircle } />
                    <Skill tech={ faReact }         star1= { faCircle }  star2= { faCircle }    star3= { faCircle } />
                    <Skill tech={ faNode }          star1= { faCircle }  star2= { faCircle }   />
                    </div>
                </div>
            </div>

            
        )
    }
    
}

export default Skills



