import React, { Component } from 'react'
import AnchorLink from 'react-anchor-link-smooth-scroll'



export class Nav extends Component {
    render() {

        return (
            <nav className="d-flex justify-content-center ">
                <div className="nav__wrapper">
                <ul className="navbar-nav navbar-expand-lg">
                    <li  className="nav-item">
                    <AnchorLink className="text-white " href='#profile'>Profile</AnchorLink>
                    </li>
                    <li className="nav-item">
                        <AnchorLink className="text-white" href='#work'>Work</AnchorLink>
                    </li>
                                     <li className="nav-item">
                        <AnchorLink className="text-white" href='#skills'>skills</AnchorLink>
                    </li>
                    <li className="nav-item">
                        <AnchorLink className="text-white" href='#contact'>Contact</AnchorLink>
                    </li>


                </ul></div>
            </nav>

            
        )
    }
}

export default Nav
