import React, { Component } from 'react'
import InnerGallery from './Inner-gallery';
import Contact from './Contact'
import Skills from './Skills'

export class Main extends Component {
    render() {
        return (
            <div>
                
                <section id="work">
                    <InnerGallery/>
                </section>
                <section id="skills" className="bg-light">
                    <Skills />
                </section>
                <section id="contact">
                    <Contact/>
                </section>
            </div>
        );
    }
}

export default Main
