import React, { Component } from 'react'
import AnchorLink from 'react-anchor-link-smooth-scroll'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFacebookSquare} from '@fortawesome/free-brands-svg-icons';
import { faTwitterSquare} from '@fortawesome/free-brands-svg-icons';
import { faLinkedin} from '@fortawesome/free-brands-svg-icons';
import { faInstagram} from '@fortawesome/free-brands-svg-icons';
import { faArrowCircleDown } from "@fortawesome/free-solid-svg-icons";
import { faPaperclip } from "@fortawesome/free-solid-svg-icons";


export class InnerHeader extends Component {




    render() {
        return (
            
                <div className="backgroundimg text-light d-flex flex-column justify-content-center align-items-center" >
                    <h1>I am Alfano Angelo</h1>
                    <p>Front-end Webdeveloper</p>
                    <p className="text-center">
                        <small>
                        The urge to invent has never been greater, i'm always looking to engineer and start new projects.<br />
                        Interested in working with me? Don't hesitate to contact me!
                        </small></p> 
                        <div className="social-media">
                  
                            <a href="https://www.facebook.com/aangelooww" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={ faFacebookSquare}/></a>
                            <a href="https://twitter.com/AngeloAlfanoCal" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={ faTwitterSquare}/></a>
                            <a href="https://www.linkedin.com/in/angeloalfanocal/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={ faLinkedin}/></a>
                            <a href="https://www.instagram.com/angeliinoooo/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={ faInstagram}/></a>
                            <a href="https://cvster.nl/r/e7TCE6Qiv" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={ faPaperclip } /></a>

                        </div>
                    <div className="but-down position-absolute">
                    <AnchorLink className="text-white" href='#work'><FontAwesomeIcon icon={ faArrowCircleDown }/></AnchorLink>
                    </div>
                </div>   
            
        )
    }
}

export default InnerHeader
